<template>
    <v-layout row
              justify-center>
        <v-icon @click="dialog = true">cancel</v-icon>
        <v-dialog v-model="dialog"
                  persistent
                  max-width="290">
            <v-card>
                <v-card-title class="headline">Cancel restore?</v-card-title>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="tfgprimary"
                           dark
                           round
                           @click.native="cancel">Go ahead!</v-btn>
                    <v-btn color=""
                           light
                           round
                           @click.native="dialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-layout>
</template>

<script>
export default {
  props: ["item"],
  data() {
    return {
      dialog: false
    };
  },
  methods: {
    cancel() {
      this.$emit("cancel");
      this.dialog = false;
    }
  }
};
</script>
