<template>
  <v-data-table :headers="headers"
                :items="this.restoreResults"
                :loading=false
                item-key="id"
                :search="search"
                :rows-per-page-items="rowsperpage"
                :pagination.sync="pagination"
                class="px-2">

    <template slot="items"
              slot-scope="props">
      <tr>
        <td class="text-xs-center px-1">{{ props.item.changeNumber }}</td>
        <td class="text-xs-center px-1">{{ props.item.owner }}</td>
        <td class="text-xs-center px-1">{{ props.item.sourceServerName }}</td>
        <td class="text-xs-center px-1">{{ props.item.sourceDatabaseName }}</td>
        <td class="text-xs-center px-1">{{ props.item.destinationServerName }}</td>
        <td class="text-xs-center px-1">{{ props.item.destinationDatabaseName }}</td>
        <td class="text-xs-center px-1">{{ props.item.plannedRestoreDate }}</td>
        <td class="text-xs-center px-1">{{ props.item.currentStatus }}</td>
        <td>
          <CancelDialog :item="props.item"
                     @cancel="cancelItem(props.item)"
                     v-if="props.item.currentStatus=='Pending'">
          </CancelDialog>
        </td>
      </tr>
    </template>
  </v-data-table>
</template>

<script>
import { CancelRestore } from "@/services/api/SQLRestores";
import { Notify } from "@/services/HelperService";
import CancelDialog from "@/components/SQL/CancelDialog";

export default {
  components: {
    CancelDialog
  },
  props: ["restoreResults", "search"],
  data() {
    return {
      pagination: {
        sortBy: "dateCreated",
        descending: true
      },
      rowsperpage: [
        10,
        15,
        25,
        {
          text: "$vuetify.dataIterator.rowsPerPageAll",
          value: -1
        }
      ],
      headers: [
        {
          text: "Change Number",
          align: "center",
          sortable: true,
          value: "changeNumber",
          class: "pl-1"
        },

        {
          text: "Owner",
          align: "center",
          sortable: true,
          value: "owner",
          class: "pl-1"
        },
        {
          text: "Source Server",
          align: "center",
          sortable: true,
          value: "sourceServerName",
          class: "pl-1"
        },
        {
          text: "Source Database",
          align: "left",
          sortable: true,
          value: "sourceDatabaseName",
          class: "pl-1"
        },
        {
          text: "Destination Server",
          align: "center",
          sortable: true,
          value: "destinationServerName"
        },
        {
          text: "Destination Database",
          align: "center",
          sortable: true,
          value: "destinationDatabaseName"
        },
        {
          text: "Restore Date",
          align: "center",
          class: "pl-1",
          value: "plannedRestoreDate"
        },
        {
          text: "Status",
          align: "center",
          sortable: true,
          value: "currentStatus",
          class: "mr-1"
        },
        {
          text: "Actions",
          align: "center",
          sortable: true,
          value: "actions",
          class: "pl-3"
        }
      ]
    };
  },
  methods: {
    cancelItem(item) {
      CancelRestore(item)
        .then(data => {
          item.currentStatus = data.currentStatus;
          this.$notify({
            group: "notify",
            type: "success",
            title: "Cancellation",
            text: `Change ${item.changeNumber} cancelled successfully!`
          });
        })
        .catch(error => {
          this.$notify(Notify(error));
        });
    }
  }
};
</script>
